import { MarketerHomePage } from './../pages/home/marketerhome';

import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule, Modal } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { LoginPage } from '../pages/login/login';
import { RegisterPage } from '../pages/register/register';
import { AdsPage } from '../pages/ads/ads';
import { PackagePage } from '../pages/package/package';
import { SettingPage } from './../pages/setting/setting';
import { VendorProfilePage } from '../pages/vendorprofile/vendorprofile';
import { MarketerProfilePage } from './../pages/marketerprofile/marketerprofile';
import { MarketerAdsPage } from './../pages/marketerads/marketerads';
import { ModalAdsPage } from './../pages/marketerads/modalads/modalads';
import { UploadMarketerPage } from './../pages/uploadmarketer/uploadmarketer';
import { UploadVendorPage } from './../pages/uploadvendor/uploadvendor';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';
import { Camera } from '@ionic-native/camera';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    LoginPage,
    RegisterPage,
    AdsPage,
    PackagePage,
    VendorProfilePage,
    MarketerAdsPage,
    MarketerProfilePage,
    ModalAdsPage,
    UploadVendorPage,
    UploadMarketerPage,
    // just testing
    MarketerHomePage,
    // just testing
    SettingPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LoginPage,
    RegisterPage,
    AdsPage,
    PackagePage,
    VendorProfilePage,
    MarketerAdsPage,
    MarketerProfilePage,
    ModalAdsPage,
    UploadVendorPage,
    UploadMarketerPage,
    // just testing
    MarketerHomePage,
    // just testing
    SettingPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    FileTransfer,
    // FileUploadOptions,
    FileTransferObject,
    File,
    Camera
  ]
})
export class AppModule {}



