import { Component } from '@angular/core';
import { NavController, AlertController } from 'ionic-angular';
import { LoginPage } from '../login/login';

@Component({
  selector: 'page-register',
  templateUrl: 'register.html'
})
export class RegisterPage {

  regType : any;

  constructor(public navCtrl: NavController,
    public alertCtrl: AlertController
    ) {

  }

  // submit register
  registerBegin() {
    let prompt = this.alertCtrl.create({
      title: 'Verifikasi',
      message: 'Masukkan Kode Verifikasi Anda',
      inputs: [
        {
          name: 'verificationCode',
          placeholder: 'Kode Verifikasi',
        },
      ],
      buttons: [
        {
          text: 'Cancel',
          handler: data => {
            console.log("cancel")
            // this.waitConfirm('Kode Salah', 'Kode yang Anda Masukkan Salah' )
          }
        },
        {
          text: 'Submit',
          handler: data => {
            console.log("submit")
            this.waitConfirm('Konfirmasi', 'Kode verifikasi telah diterima, silahkan login menggunakan username dan kata sandi terdaftar' )
          }
        }
      ]
    })
    prompt.present()
  }

  waitConfirm(titlePrompt, messagePrompt) {
    if (titlePrompt == 'Konfirmasi') {
      let confirm = this.alertCtrl.create({
        title: titlePrompt,
        message: messagePrompt,
        buttons: [
          {
            text: 'Login',
            handler: data => {
              this.navCtrl.push(LoginPage);
            }
          }
        ]
      })
    
      confirm.present()
    }
    else {
      let confirm = this.alertCtrl.create({
        title: titlePrompt,
        message: messagePrompt,
        buttons: [
          {
            text: 'Cancel',
            handler: data => {
              this.registerBegin();
            }
          }
        ]
      })
    
      confirm.present()
    }
  }
  // submit register

  // back to login
  login() {
    this.navCtrl.push(LoginPage)
  }
  // back to login

}



